.review {
  align-self: flex-start;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 16px;

  &__num {
    display: flex;
    font-size: $secondary-extra-sm-font;
    line-height: 14px;
    font-weight: $font-weight-normal;
    color: $text-primary-color;
  }
}
